  flex-direction: row;
  align-items: flex-start;
  padding: 10px 12px;
  width: 42px;
  height: 42px;
}

.analysis-units-units img:nth-child(1) {
  border-radius: 4px 0px 0px 0px;
}

.analysis-units-units img:nth-child(5) {
  border-radius: 0px 0px 0px 4px;
}

.analysis-unit-selected {
  z-index: 1;
  border-width: 1px 0px 1px 1px !important;
  background-color: $background05 !important;
  padding: 9px 13px 9px 11px !important;
  border-style: solid;
  border-color: $divider;
  border-radius: 4px 0px 0px 4px;
}

.analysis-units-items {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  padding: 14px 0px 14px 40px;
  overflow-y: scroll;
  height: calc(100% - 28px);
}

.analysis-units-items::-webkit-scrollbar{
  background: $background09;
  border-left: 1px solid $divider;
  width: 12px;
 }
 
 .analysis-units-items::-webkit-scrollbar-thumb{
  background-color: $buttonSelected;
  border-radius: 20px;
  width: 8px;
  height: 32px;
 }

.analysis-item-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px;
  gap: 7px;
  padding: 12px 0px;
  border-bottom: 1px solid $divider;
}



.analysis-item-container img {
  width: 32px;
  height: 32px;
}

.analysis-item-stats {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0px;
}

.analysis-item-bar {
  display: flex;
  width: 290px;
}

.analysis-item-blue {
  width: 185px;
  height: 10px;
  background-color: $primary;
}

.analysis-item-white {
  height: 10px;
  width: 105px;
  background-color: $white600;
}

.analysis-augments-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  width: 514px;
  height: 306px;
  border: 1px solid $divider;
  border-radius: 4px 4px 4px 4px;
}

.analysis-augments-titles {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 2px 18px;
  gap: 74px;
  width: 478px;
  height: 30px;
  background: $background09;
  border-radius: 4px 4px 0px 0px;
  border-bottom: 1px solid $divider;
}

.analysis-augments-inner {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  height: calc(100% - 30px);
  overflow-y: scroll;
}

.analysis-augments-inner::-webkit-scrollbar{
 background: $background09;
 border-left: 1px solid $divider;
 width: 12px;
}

.analysis-augments-inner::-webkit-scrollbar-thumb{
 background-color: $buttonSelected;
 border-radius: 20px;
 width: 8px;
 height: 32px;
}

.analysis-augments-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0px 18px;
  width: 478px;
  height: 28px;
  background: $background05;
  border-bottom: 1px solid $divider;
}

.analysis-augments-row img {
  width: 51px;
  height: 28px;
  object-fit: contain;
}

.analysis-augments-row .avg {
  width: 108px;
}

.analysis-augments-row .winrate {
  width: 44px;
}

.analysis-augments-row .freq {
  width: 46px;
}

.analysis-augments-row:nth-child(even) {
  background-color: $background09;
}

.analysis-augments-row p {
  text-align: center;
}

.analysis-placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 8px;
  background-color: $background05;
  border: 1px solid $divider;
  border-radius: 4px;
  height: 160px;
  margin-bottom: 20px;
}

.analysis-wrapper {
  display: flex;
  flex-direction: row;
  gap: 20px;
}

.builder-board-buttons-container {
  position: absolute;
  right: 8px;
  top: 8px;
  display: flex;
  flex-direction: row;
  gap: 8px;
}

.builder-board-clear-button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  border-radius: 4px;
  border: 1px solid $divider;
  background-color: $background09;
  padding: 6px 12px 6px 8px;
  cursor: pointer;
  transition: all cubic-bezier(0.84, 0.15, 0.21, 0.92) 0.2s;
}

.builder-board-copy-button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  border-radius: 4px;
  border: 1px solid $divider;
  background-color: $primary;
  padding: 6px 12px 6px 8px;
  cursor: pointer;
  transition: all cubic-bezier(0.84, 0.15, 0.21, 0.92) 0.2s;
}

.builder-board-embed-button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  border-radius: 4px;
  border: 1px solid $divider;
  background-color: $primary;
  padding: 6px 12px 6px 8px;
  cursor: pointer;
  transition: all cubic-bezier(0.84, 0.15, 0.21, 0.92) 0.2s;
}

.builder-board-embed-button img{
  width: 18px;
  height: 18px;
}

.builder-board-copy-button:hover {
  background-color: $buttonSelected;
}

.builder-board-clear-button:hover {
  background-color: $buttonHover;
}

.builder-board-clear-button img {
  width: 18px;
  height: 18px;
}

.builder-board-copy-button img {
  width: 18px;
  height: 18px;
}

.lds-dual-ring {
  display: inline-block;
  width: 60px;
  height: 60px;
}
.lds-dual-ring:after {
  content: " ";
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin: 6px;
  border-radius: 50%;
  border: 4px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-dual-ring 2s linear infinite;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.table-loader-wrapper {
  width: 100%;
  background-color: $background05;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

.unit-hex-remove-button {
  display: none;
  justify-content: center;
  align-items: center;
  padding: 2px;
  position: absolute;
  right: -8px;
  top: 14px;
  background-color: #b83d3d;
  z-index: 10;
  border-radius: 10px;
  cursor: pointer;
}

.unit-hex-container-medium:hover .unit-hex-remove-button {
  display: flex;
  opacity: 1;
}

.unit-hex-remove-button:hover {
  opacity: 0.7;
}

.unit-hex-remove-button img {
